@media only screen and (max-width: 600px){
	.expand-panel{
		width: auto;
		height: auto;
	}
	.firstProject{
		margin-top: 75px !important;
	}
	.toolbar-text-field{
		-ms-flex: 1 1;
		    flex: 1 1;
	}
	.toolbar-back-btn{
		color: white;
	}
}

@media only screen and (min-width: 601px){
	.expand-panel{
		width: 500px;
		height: auto;
	}
	.firstProject{
		margin-top: 100px !important;
	}
	.toolbar-label{
		-ms-flex: 0.3 1;
		    flex: 0.3 1;
	}
	.toolbar-text-field{
		-ms-flex: 0.4 1;
		    flex: 0.4 1;
	}
	.toolbar-back-btn{
		color: white;
	}
}


